import { CCard, CCardHeader, CCol, CRow, CCardBody } from '@coreui/react'
import React, { lazy, useState, useEffect } from 'react'
import MainChartExample from '../charts/MainChartExample.js'
import BarChartExample from '../charts/ChartBarSimple.js'
import {
    CChartBar,
    CChartLine,
    CChartDoughnut,
    CChartRadar,
    CChartPie,
    CChartPolarArea
} from '@coreui/react-chartjs'
import { Select } from 'antd'
import { Button } from 'react-bootstrap'

import ChartSumEmp from 'src/components/nkp-design/chartSumEmp.js'

const Summary = () => {

    const [data, setData] = useState({

        chart: {
            dp_id: "",
        },

    });

    const [selects, setSelects] = useState({
        dept: {
            option: [{ id: "1", name: "NKP" },
            { id: "2", name: "PCBA" },
            { id: "3", name: "PMD" },
            { id: "4", name: "IMO" },
            { id: "5", name: "IAD" },
            ],
            selected: "",
        }
    });

    useEffect(() => {
        _fetchData();
    }, []);


    const _fetchData = async () => {
        setData({

            chart: {
                dp_id: selects.dept.selected === "" ? "" : selects.dept.option[selects.dept.selected - 1].id,
            },

        });
    };

    const _setChart = async () => {
        setData({

            chart: {
                dp_id: selects.dept.selected === "" ? "" : selects.dept.option[selects.dept.selected - 1].id,
            },

        });
    };
    const _clearChart = async () => {
        setData({

            chart: {
                dp_id: "",
            },

        });
    };


    return (

        <CCard>

            <CCardHeader>
                <h4>Summary Manpower</h4>
            </CCardHeader>
            <CCardBody>
                <CRow>
                    <CCol sm="2">
                        <Select
                            style={{ width: "130px" }}
                            value={
                                selects.dept.selected === ""
                                    ? selects.dept.option[0].name
                                    : selects.dept.option[selects.dept.selected - 1].name
                            }
                            onChange={(val) => {

                                setSelects({
                                    ...selects,
                                    dept: { ...selects.dept, selected: val },
                                });


                            }
                            }

                        >
                            {selects.dept.option.map((data) => (
                                <Select.Option key={data.id}>
                                    {data.name}
                                </Select.Option>
                            ))}
                        </Select>
                    </CCol>
                    <CCol sm="2">
                        <Button
                            className="btn btn-primary"
                            style={{ width: "80px", float: "left" }}
                            onClick={() => _setChart()}
                        >Set</Button>
                    </CCol>
                    <CCol sm="2">
                        <Button
                            className="btn btn-danger"
                            style={{ width: "80px", float: "left" }}
                            onClick={() => _clearChart()}
                        >Clear</Button>
                    </CCol>
                </CRow>
                <br />
                <CRow>
                    <CCol sm="12">
                        {/* <CChartBar
                            datasets={[
                                {
                                    label: 'GitHub Commits',
                                    backgroundColor: '#f87979',
                                    data: [40, 20, 12, 39, 10, 40, 39]
                                }
                            ]}
                            labels="months"
                            options={{
                                tooltips: {
                                    enabled: true
                                }
                            }}
                        /> */}
                        <ChartSumEmp
                            dp_id={data.chart}
                        ></ChartSumEmp>


                    </CCol>

                </CRow>
            </CCardBody>

        </CCard>

    )


}

export default Summary