import React, { useState, useEffect } from "react";
import { Bar, Line, Scatter } from 'react-chartjs-2';
import Loading from "./Loading";
import AttModel from '../../models/AttendenceModel';
import { Tag } from "antd";
import { useSelector } from "react-redux";
import ChartModel from 'src/models/ChartModel.js';


const chart_model = new ChartModel();

export default function ChartSumEmp(props) {

    const [loading, setLoading] = useState(false);
    const [labels, setLabels] = useState([]);
    const [dset, setDset] = useState([]);
    const [dset2, setDset2] = useState([]);
    const [line, setLine] = useState([]);
    const [dept,setDept] = useState([]);

    let llabels = [];
    let ddset = [];
    let ddset2 = [];
    let lline = [];
    var nameD =null;

    useEffect(() => {
        _fetchData();
    }, [props]);

    const _fetchData = async () => {
        ///console.log("check: ",props.dp_id);
        setLoading(true);
        // const result = await chart_model.getManCountChart({

        //     dp_id: 1,
        //     //post_id:"",

        // });
        let result;

        if (props.dp_id.dp_id === "1") {
            result = await chart_model.getManCountChartNKP();
            setDept("NKP");
        }
        else if (props.dp_id.dp_id === "2") {
            result = await chart_model.getManCountChartPCBA();
            setDept("PCBA");
        }
        else if (props.dp_id.dp_id === "3") {
            result = await chart_model.getManCountChartPMD();
            setDept("PMD");
        }
        else if (props.dp_id.dp_id === "4") {
            result = await chart_model.getManCountChartIMO();
            setDept("IMO");
        }
        else if (props.dp_id.dp_id === "5") {
            result = await chart_model.getManCountChartIAD();
            setDept("IAD");
        }
        else {
            result = await chart_model.getManCountChartALL();
            
        }

        console.log("check:", result);
        for (var i = 0; i < result.length; i++) {

            if (result[i].po_count != 0) {
                llabels.push(result[i].po_name);
                ddset.push(result[i].po_count);
            }
            //ddset2.push(result[i].absent);

        }

        setLabels(llabels);
        setDset(ddset);
        //setDset2(ddset2);

        //console.log("label:",labels);


        setLoading(false);
    };

    return (
        <>
            <Loading
                show={loading}
            />
            {props.dp_id.dp_id ===""? <Tag color="#2db7f5" style={{fontSize:"18px"}}>MFG2</Tag>: <Tag color="#2db7f5" style={{fontSize:"18px"}}> Dept : {dept}</Tag>}
            <Bar
                //hidden={props.show}
                options={{
                    responsive: true,
                    plugins: {
                        // legend: {
                        //     position: 'bottom'
                        // }
                    },

                }}
                data={{
                    labels: labels,
                    datasets: [
                        {
                            label: 'Actual',
                            data: dset,
                            type: 'bar',
                            borderColor: "blue",
                            backgroundColor: "blue",


                        },
                        // {
                        //     label: 'Present',
                        //     data: dset,
                        //     borderColor: 'rgb(11, 218, 81)',
                        //     backgroundColor: 'rgba(11, 218, 81, 0.5)',
                        //     stack: 'Stack 0',
                        //     type: 'bar'
                        //     // yAxisID:'y'
                        // },
                        // {
                        //     label: 'Absent',
                        //     data: dset2,
                        //     borderColor: 'rgb(255, 99, 132)',
                        //     backgroundColor: 'rgba(255, 99, 132, 0.5)',
                        //     stack: 'Stack 0',
                        //     type: 'bar'
                        //     //yAxisID:'y'
                        // },

                    ],

                }}
            />
        </>
    );



}