import { BaseModel } from "./MainModel";


export default class Att extends BaseModel {

    async getAllAtt(data){
        //console.log("GetATT:",data);
        return this.directFetch({
            url:"Att",
            method:"POST",
            body: JSON.stringify(data),
        });
    }

    async getFromFort(data){
        return this.directFetch2({
            url:"AttendanceMFG2",
            method:"POST",
            body: JSON.stringify(data),
        });
    }

    async saveAtt(data){
        this.directFetch({
            url:"InsertAtt",
            method:"POST",
            body: JSON.stringify(data),
        });
    }

    

    async getShift()
    {
        return this.directFetch({
            url:"Shift",
            method:"GET",
            //body: JSON.stringify(data),
        });
    }
}
